  this.expanded = ''\n          },\n          backToMenu () {\n            this.$router.push('menu')\n          }\n        }\n    }\n</script>\n\n<style scoped>\n  @import url('https://fonts.googleapis.com/css?family=Poiret+One');\n  #page {\n    width:100%;\n    height: 100vh;\n  }\n  #grid {\n    max-width: 50rem;\n    max-height: 90%;\n    width: 90vw;\n    height: 90vw;\n    top:50%;\n    right: 50%;\n    position: absolute;\n    transform: translate(50%, -50%);\n    min-width: 200px;\n    min-height: 200px;\n    display: flex;\n    justify-content: center;\n    flex-wrap: wrap;\n    font-family:\"Poiret One\";\n    z-index: 1;\n  }\n  #back {\n    z-index: 0;\n    width: 100%;\n    height: 100%;\n    position: absolute;\n    background-color: rgb(128, 187, 128);\n  }\n\n  .gridTile {\n    flex: 1 1 30%;\n    margin-right:3%;\n    margin-top:3%;\n    min-height: 30%;\n    height: 30%;\n    border: solid black 1px;\n    border-radius: 3px;\n    position: relative;\n    transition: ease 0.3s all;\n    z-index: 1;\n    background-color: white;\n    display: flex;\n    flex-direction: column;\n  }\n\n  .gridTile:hover {\n    cursor: pointer;\n    background-color: rgba(225, 195, 255, 0.829);\n    transform: scale(1.1);\n    border-radius: 8px;\n    border: none;\n  }\n\n  .gridTile.expanded {\n    flex-basis: 100%;\n    width:100%;\n    min-height: 100%;\n    align-self: center;\n    justify-self: center;\n    order: 1;\n    z-index:2;\n    margin:0;\n    position: absolute;\n    border: none;\n  }\n  .expanded:hover{\n    cursor: default;\n    background-color: white;\n    transform: none;\n    border-radius: 3px;\n    border: none;\n  }\n  .placeholder{\n    opacity: 0.7;\n    pointer-events: none;\n  }\n\n  .title {\n    text-decoration: none;\n  }\n\n  .titleText {\n    display: flex;\n    justify-content: center;\n    font-size: 1.7rem;\n    margin:0;\n    padding: 10px;\n    transition: ease 0.3s all;\n  }\n  .titleText:hover{\n    background-color: rgba(206, 