  project.displayURL =\n          project.url.replace('api.github.com/repos', 'github.com')\n        return project\n      })\n  },\n  data () {\n    return {\n      projects: [],\n      hovered: false\n    }\n  },\n  methods: {\n    projectHover (event) {\n      setTimeout(() => {\n        this.hovered = true\n      }, 300)\n    },\n    projectLeave () {\n      this.hovered = false\n    },\n    projectClick (url) {\n      if (this.hovered) {\n        window.open(url, '_blank')\n      }\n    }\n  }\n}\n</script>\n\n<style scoped>\n@import url(\"https://fonts.googleapis.com/css?family=Poiret+One\");\n#projects {\n  font-family: \"Consolas\";\n  position: absolute;\n  top: 50%;\n  left: 50%;\n  width: 70%;\n  height: 70%;\n  transform: translate(-50%, -50%);\n  text-align: center;\n  overflow-y: auto;\n  padding: 1em;\n}\n.project {\n  position: relative;\n  display: flex;\n  flex-direction: column;\n  margin-bottom: 10%;\n}\n.project * {\n  margin-top: 10px;\n}\n.project h2 {\n  font-family: \"Poiret One\";\n  margin-bottom: 5px;\n  font-size: 2em;\n}\n.project a {\n  text-decoration: none;\n}\n.project .imgContainer {\n  width: 50%;\n  transform: translateX(50%);\n  cursor: pointer;\n}\n.project .imgContainer img {\n  max-height: 90%;\n  max-width: 100%;\n  transition: all 0.3s;\n}\n.project .imgContainer .desc {\n  transition: all 0.3s;\n  font-size: 1rem;\n  width: 100%;\n  opacity: 0;\n  position: absolute;\n  font-family: \"Consolas\";\n  top: 0px;\n  transform: translateY(8em);\n}\n.project .imgContainer:hover .desc {\n  display: block;\n  opacity: 1;\n  transform: translateY(1em);\n}\n.project .imgContainer:hover img {\n  opacity: 0.1;\n}\n#back {\n  position: absolute;\n  bottom: 10%;\n  left: 50%;\n  transform: translate(-50%, -50%);\n  text-decoration: none;\n  color: black;\n  font-family: \"Poiret One\";\n  font-size: 2em;\n  transition: ease-in-out 0.3s all;\n  background-color: white;\n  padding: 5px;\n}\n#back:hover {\n  cursor: pointer;\n  opacity: 1;\n  padding: 10px;\n}\n.meta {\n  font-size: 1em;\n}\n.arrow {\n  font-size: 1em;\n}\n.count {\n  font-family: \"Consolas\";\n  font-size: 1em;\n  margin: 0;\n}\n.fade-enter-active,\n.fade-leave-active {\n  transition: opacity 0.3s ease;\n}\n.fade-enter, .fade-leave-to /* .fade-leave-active below version 2.1.8 */ {\n  opacity: 0;\n}\n@media only screen and (max-width: 500px) {\n  #projects {\n    width: 100%;\n    height: 300px;\n    transform: translate(-50%, -70%);\n  }\n  #count {\n    display: none;\n  }\n  #meta {\n    display: none;\n  }\n  #scrollbarHide {\n    display: none;\n  }\n  .project .imgContainer {\n    height: 180px;\n  }\n}\n</style>\n"],"sourceRoot":""}]);

